import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Container,
  Card,
  CardBody,
  Table,
  Row,
  Col,
  Label,
  Input,
} from "reactstrap";
import { toast } from "react-toastify";
import { getListAppointment } from "../../../redux/actions/appointmentAction";
import PaginationBar from "../../../components/PaginationBar";
import Loading from "../../../components/Loading";
import Appointment from "./Appointment.jsx";
import "./ListAppointment.scss";
import FloorMapModal from "./FloorMapModal.jsx";
import { useState } from "react";
import { useEffect } from "react";

export default function ListAppointment(props) {
  const dispatch = useDispatch();
  const appointmentData = useSelector((state) => state.appointment.data);
  const loading = useSelector((state) => state.appointment.loading);
  const [isOpenFloorMapModal, setIsOpenFloorMapModal] = useState(false);
  const toggleFloorMapModal = () => {
    setIsOpenFloorMapModal(!isOpenFloorMapModal);
  };
  const [venueId, setVenueId] = useState("");
  const [limit, setLimit] = useState(5);
  const [offset, setOffset] = useState(0);
  const [selected, setSelected] = useState(0);
  const [ordering, setOrdering] = useState("created_at");
  const [desc, setDesc] = useState(false);

  useEffect(() => {
    dispatch(getListAppointment({ limit, offset, selected, ordering, desc }));
  }, [limit, offset, selected, ordering, desc]);

  const sort = (e) => {
    let name = e.target.attributes.name.value;
    if (name && name === ordering) {
      setDesc(!desc);
    } else {
      setOrdering(name);
      setDesc(true);
    }
    resetFilterData();
  };

  const selectPageSize = (e) => {
    const { value } = e.target;
    if (value) {
      resetFilterData();
      setLimit(value);
    }
  };

  const selectPage = ({ selected }) => {
    setSelected(selected || 0);
    setOffset(Math.ceil(selected * limit));
  };

  const resetFilterData = () => {
    setOffset(0);
    setSelected(0);
  };

  const openFloorMapModal = (appointment) => {
    if (appointment.space && appointment.space.venue.id) {
      setIsOpenFloorMapModal(true);
      setVenueId(appointment.space.venue.id);
    } else {
      toast.warn("Alternate venue!");
    }
  };

  const listAppointment = appointmentData.result;
  const totalPage = Math.ceil(appointmentData.count / limit) || 0;
  const paginationInfo = {
    from: selected * limit + 1,
    to:
      (selected + 1) * limit < appointmentData.count
        ? (selected + 1) * limit
        : appointmentData.count,
    total: appointmentData.count,
    getShowOne: function () {
      return this.from === this.to;
    },
  };
  const paginationSetting = {
    totalPage,
    selected: selected,
  };
  const sortClass = {
    title: ordering,
    value: desc,
    get: function (name) {
      if (this.title === name) {
        return `sort ${this.value ? "sort-desc" : "sort-asc"}`;
      }
      return "sort";
    },
  };
  return (
    <Container fluid className="list-appointment">
      <Card className="mt-3">
        {loading && <Loading />}
        <CardBody>
          <Table className="align-items-center table-sort" responsive>
            <thead className="thead-light">
              <tr>
                {/* <th className="w-95"></th> */}
                <th
                  className={`${sortClass.get(
                    "booking__start_date,start_time"
                  )}`}
                  name="booking__start_date,start_time"
                  onClick={sort}
                >
                  Date
                </th>
                <th
                  className={` ${sortClass.get("booking__space__name")}`}
                  name="booking__space__name"
                  onClick={sort}
                >
                  Space
                </th>
                <th
                  className={` ${sortClass.get("duration")}`}
                  name="duration"
                  onClick={sort}
                >
                  Duration
                </th>
              </tr>
            </thead>
            <tbody className="client">
              {listAppointment &&
                listAppointment.map((elm, i) => {
                  return (
                    <Appointment
                      onclick={() => openFloorMapModal(elm)}
                      item={elm}
                      key={i}
                    />
                  );
                })}
            </tbody>
          </Table>
          {listAppointment && listAppointment.length > 0 && (
            <Row className="d-flex justify-content-md-between justify-content-sm-start mb-3 mx-0 mt-2">
              <Col className="d-flex justify-content-center">
                {totalPage > 1 && (
                  <PaginationBar
                    setting={paginationSetting}
                    handleClick={selectPage}
                  />
                )}
              </Col>
              <Col
                className="d-flex align-items-baseline mb-md-0 mb-2"
                xs="auto"
              >
                <Input
                  onChange={selectPageSize}
                  value={limit}
                  bsSize="md"
                  type="select"
                  name="selectPageSize"
                  id="selectPageSize"
                >
                  <option value="5">5</option>
                  <option value="10">10</option>
                  <option value="20">20</option>
                  <option value="50">50</option>
                </Input>
                <Label className="ml-1 text-nowrap">
                  {/* <small>
                    {`${paginationInfo.from} ${
                      paginationInfo.getShowOne()
                        ? ""
                        : `- ${paginationInfo.to}`
                    } of ${paginationInfo.total}`}
                  </small> */}

                  <small>
                    Page {`${selected + 1}`} of {`${totalPage}`}
                  </small>
                </Label>
              </Col>
            </Row>
          )}
        </CardBody>
      </Card>
      {isOpenFloorMapModal && (
        <FloorMapModal
          isOpen={isOpenFloorMapModal}
          toggle={toggleFloorMapModal}
          venueId={venueId}
        />
      )}
    </Container>
  );
}
