import React from 'react';
import {
    Modal
} from 'reactstrap';
import { useState } from 'react';
import { useEffect } from 'react';
import venueApi from '../../../api/venueApi'
import default_image from '../../../assets/img/theme/default-image.png';
import { getImageMeta, convertToDecimal } from '../../../helpers/UtilsHelper';

export const containerWidth = Math.ceil(((document.body.clientWidth - 250 - 60 - 48 + 30) * 0.5) * 7 / 12);


export default function FloorMap(props) {
    const [roomMap, setRoomMap] = useState(default_image)
    const { isOpen, toggle, venueId } = props
    const [imgSize, setImgSize] = useState({});

    useEffect(() => {
        if (venueId) {
            venueApi.detail(venueId).then(res => {
                const width = containerWidth;
                getImageMeta(res.roomMap).then(meta => {
                    const height = convertToDecimal(meta.height / meta.width * width, 4);
                    setImgSize({width, height});
                    setRoomMap(res.roomMap);
                });
            }).catch(console.log)
        }
    }, [])

    return (
        <Modal
            className="modal-dialog-centered"
            isOpen={isOpen}
            toggle={toggle}
            style={{maxWidth: `${containerWidth + 100}px`}}
        >
            <div className="modal-header">
                <h6 className="modal-title" id="modal-title-default">
                    Floor map
                </h6>
                <button
                    aria-label="Close"
                    className="close"
                    data-dismiss="modal"
                    type="button"
                    onClick={toggle}
                >
                    <span aria-hidden={true}>Ã—</span>
                </button>
            </div>
            <div className="modal-body text-center">
                <img
                    className="floor-map-img"
                    alt="floor map"
                    src={roomMap}
                    width={imgSize.width}
                    height={imgSize.height}
                />
            </div>
        </Modal>
    );
}