import React from "react";
import _ from "lodash";
import * as moment from "moment";
import { getDurationTime, getDateTime } from "helpers/UtilsHelper";
import avatar from "../../../assets/img/theme/default-avatar.png";
import default_image from "../../../assets/img/theme/default-image.png";
import cons from "../../../constants";
import { Button } from "reactstrap";

export default function Appointment(props) {
  const { item, onclick } = props;
  const duration = getDurationTime(
    moment(item.startTime, "HH:mm:ss"),
    moment(item.endTime, "HH:mm:ss")
  );
  const unit = duration > 1 ? "Hours" : "Hour";
  const spaceImg = _.get(
    item && item.space,
    "space.images[0].file",
    default_image
  );
  const spaceName = _.get(item, "space.name", "Non-Ethera");
  const venueName = _.get(item, "space.venue.name", "Non-Ethera");
  return (
    <tr
      className={
        moment(item.startDate).isBefore(new Date()) ||
        item.status !== cons.BOOKING_STATUS.ACTIVE
          ? "pass-appointments"
          : ""
      }
    >
      {/* <td className="table-actions text-center table__width-100">
        <img alt="..." className="avatar" src={spaceImg || avatar} />
      </td> */}
      <td className="table-actions">
        {getDateTime(
          `${item.startDate} ${item.startTime}`,
          "MM/DD/YYYY hh:mmA"
        )}
      </td>
      <td className="table-actions">
        <Button color="link" className="space-name" onClick={onclick}>
          {spaceName} | {venueName}
        </Button>
      </td>
      <td className="table-actions">
        {duration} {unit}
      </td>
    </tr>
  );
}
